.theme-green {
  background-color: #43e629 !important;
}

.theme-blue {
  background-color: #1ecbe1 !important;
}

.theme-orange {
  background-color: #fbb408 !important;
}

.theme-dark-blue {
  background-color: #07498b !important;
}

.theme-dark-green {
  background-color: #1d5818 !important;
}

.theme-dark-red {
  background-color: #6d0000 !important;
}

.main {
  position: fixed;
  font-family: "Soleil_Medium",Helvetica,Arial,sans-serif;
  align-items: center;
  background-image: url('https://sara-chan-bot.github.io/img/wave_bg.png');
  background-repeat: no-repeat;
  background-position: left;
  color: #fff;
  min-height: 100%;
  width: 100%;
  display: -ms-flexbox;
  -ms-flex-pack: distribute;
  display: flex;
  justify-content: space-around;
  display: -webkit-flex;
  -webkit-justify-content: space-around;
  -webkit-align-items: space-around;
  font-size: 1.2rem;
}

.home {
  position: fixed;
  height: 100%;
  text-align: center;
  width: 100vw;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.main-content {
  margin-top: 1rem;
  align-items: center;
  height: 100%;
  width: 90%;
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.title-block {
  text-align: left;
  width: 75%;
}

.speechup-logo {
  max-width: 100%;
}

.speechup-description {
  max-width: 100%;
}

/** Desktop **/
@media only screen and (min-width: 1000px) {
  .main-content {
    width: 80%;
    flex-direction: row;
    justify-content: center;
    display: flex;
  }

  .title-block {
    text-align: left;
    width: 50%;
  }

  .home {
    height: 95%;
  }

  .speechup-logo {
    max-height: 6rem;
  }
  
  .speechup-description {
    max-height: 3.5rem;
  }
}

@media only screen and (min-width: 550px) {
  .title-block {
    width: 45%;
  }

  .main-content {
    justify-content: space-evenly;
  }
}