.social-container {
  margin: 1rem 0rem 0rem 0rem;
}

.social-list {
  display: flex;
  justify-content: center;
}

.copyrights {
  margin: .5rem 0;
  color: #000;
  font-size: .7rem;
}

.share-button {
  margin: 0px .3rem;
}

.share-count {
  justify-content: center;
  white-space: nowrap;
  overflow: visible;
  text-align: center;
  font-size: .5rem;
  color: #000;
  display: none;
}