.paddingTop {
    padding: 1.3rem 0 0 0;
}

.tts-container {
    flex-direction: column;
    display: flex;
    justify-content: center;
    text-align: center;
    width: 45%;
    height: 100%;
}

.select-input {
    width: 100%;
    flex-direction: row;
    justify-content: center;
    display: flex;
}

.lang-select {
    text-align: center;
    width: 55%;
    margin-right: 1rem;
}

.voice-select {
    text-align: center;
    width: 35%;
    margin-left: 1rem;
}

.input-container {
    width: 100%;
    height: 45%;
    text-align: center;
    margin-bottom: 1.3rem;
    background-color: white;
    border-radius: 1rem;
    box-shadow: rgb(0 0 0 / 15%) 0px 0.3rem 0.3rem;
    z-index: 1;
    padding: .5rem 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

textarea {
    width: 100%;
    height: 100%; 
    font-size: 1.1rem;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

textarea:focus {
    outline: none !important;
}

.disclaimer-container {
    color: grey;
    font-size: .7rem;
}

.file-upload-icon {
    max-height: 6rem;
}

.file-select-display-container {
    text-align: center;
    flex-direction: row;
    justify-content: center;
    display: flex;
    
}

.file-upload-text {
    color: black;
    font-size: 2.3rem;
    padding-left: 1rem;
}

.selected-file-name {
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 1.1rem;
    white-space: nowrap;
    color: grey;
    font-size: 1rem;
    padding: 0 3rem;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box; 
    width: 100%;
}

.preview-text {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: grey;
    font-size: 1rem;
    padding: 0 1.5rem;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box; 
    width: 100%;
}

.external-loader-container {
    height: 100%;
    flex-direction: column;
    display: flex;
}

.external-loader {
    flex: 1;
    flex-direction: column;
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    align-items: center;
}

.url-loader {
    flex: 1;
    flex-direction: column;
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    align-items: center;
}

.url-input-container {
    width: 90%;
    flex-direction: row;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding-right: 1rem;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box; 
}

.url-label {
    width: 40%;
    font-size: 1rem;
    font-weight: bold;
}

.url-input {
    width: 60%;
    font-size: 1rem;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box; 
}

.url-text-input {
    border-radius: 1rem;
    background-color: white;
    border-color: #ffffff;
    border-style: solid;
    border-width: 1px;
    box-shadow: rgb(0 0 0 / 15%) 0px 0.3rem 0.3rem;
    z-index: 1;
    padding: .5rem .5rem .5rem 1rem;
    outline: none;
}

.control-icon {
    background-color: white;
    border-radius: .7rem;
    max-height: 2.5rem;
    margin: 0 .5rem;
    padding: .1rem .2rem;
    box-shadow: rgb(0 0 0 / 15%) 0px 0.3rem 0.3rem;
    z-index: 1;
}

.url-button {
    margin: .5rem 0 .2rem 0;
    border-radius: 1rem;
    color: white;
    border: none;
    font-weight: bold;
    padding: .5rem 1rem;
    font-size: 1rem;
}

.img-disabled {
    opacity: .4;
}

.control-container {
    flex-direction: row;
    display: flex;
    justify-content: center;
    width: 100%;
}

.option-container {
    width: 100%;
    flex-direction: row;
    display: flex;
    justify-content: space-around;
}

.tab-button {
    border: none;
    padding:.7rem 1rem;
    font-size: 1.1rem;
    font-weight: bold;
}

.slider-container {
    width: 40%;
    padding: .5rem 1rem;
}

@media only screen and (max-width: 1000px) {
    .tts-container {
        width: 75%;
        height: 70%;
    }
}

@media only screen and (max-width: 550px) {

    .url-button {
        font-size: .8rem;
        padding: .3rem .7rem;
    }

    .url-label {
        width: 35%;
        font-size: .7rem;
        font-weight: bold;
    }

    .url-input {
        width: 65%;
        font-size: .8rem;
    }

    .tts-container {
        width: 100%;
        height: 80%;
    }

    .input-container {
        height: 35%;
        margin-bottom: .4rem;
        padding: .2rem 0;
    }

    .paddingTop {
        padding: .6rem 0 0 0;
    }

    .select-input {
        align-items: center;
        flex-direction: column;
        justify-content: center;
        display: flex;
    }

    .lang-select {
        width: 70%;
        margin-right: 0;
        margin-bottom: .4rem;
        font-size: 1rem;
    }
    
    .voice-select {
        width: 60%;
        margin-left: 0;
        font-size: 1rem;
    }

    .option-container {
        flex-direction: column;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    .slider-container {
        width: 80%; 
        padding: .1rem 1rem;
        margin: .2rem 0;
    }

    .tab-button {
        border: none;
        padding: .4rem .6rem;
        font-size: 1rem;
        font-weight: bold;
    }

    .file-select-display-container {
        width: 20%;
    }

    .selected-file-name {
        width: 55%;
        padding: 0 .5rem 0 0;
    }

    .file-upload-icon {
        max-height: 3.5rem;
    }

    .external-loader {
        padding-top: .5rem;
        flex-direction: row;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}